import React from "react";
import { useParams } from "react-router-dom";
/***********Initialise Functional Component Returns Context Container******************************************************/
const ConfigDetails = ({ data }) => {
  const { configId } = useParams();
  const configProduct = data.find(p => p.id === Number(configId));
  let configData;
 

  if (configProduct) {
    configData = (
      <div className="col-12 col-sm-12 col-md-12 col-lg-12 col-xl-12 col-xxl-12 mb-1">
          <div className="card rounded-0">
              <div className="card-header">
                  <h5 className="card-title my-1 fst-italic f-color fw-bold">{configProduct.title}</h5>                             
              </div>
              <div className="card-body" style={{height: "194px", overflowY: 'scroll'}}>
                {configProduct.uploadedFile.map((details) =>{
                    return(
                        <div className="row px-3" key={details.id}>
                          <p>SAM-Q Config Version : {details.SAM_Q_Config_Version}</p>
                          <p>Unit_Name : {details.Unit_Name}</p>
                          <p>SAM_Q_Edit_Version : {details.SAM_Q_Edit_Version}</p>
                          <p>SAM_Q_Config_Version : {details.SAM_Q_Config_Version}</p>
                          <p>Box type name : {details.Box_type_name}</p>
                          <p>Device_MAC : {details.Device_MAC}</p>
                          <p>Licenses : {details.Licenses}</p>
                          <p>SW_Version : {details.SW_Version}</p>
                          <p>HW_Version : {details.HW_Version}</p>
                          <p>Menu_PIN : {details.Menu_PIN}</p>                     
                        </div>
                    )
                  })
                }
              </div>
          </div>
      </div>
    );
  } else {
    configData = <React.Fragment></React.Fragment>;
  }

  return (
    <React.Fragment>
      {configData}
    </React.Fragment>
  );
};

export default ConfigDetails;