import React, {useRef, useContext} from 'react';
import {useHistory} from 'react-router-dom';
import './Status.css';
import { AuthContext } from '../context/AuthContext';
/***********Initialise Functional Component in React******************************************************/
/*********Return Upload Firmware Element*****************/
const Uploadfirmware = () => { 
    const [isAuthenticated,  ] = useContext(AuthContext);
    const history = useHistory();
    const firmwareFile = useRef();
    /***********Browse File Functionality******************************************************/
    function sendRequest(Obj){
        const options = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(Obj)
        }; 
       return options;
    }

    /*function restartCallback(val) {  
        // selecting loading div
        const loader = document.querySelector("#loading");  
        let requestOptions = sendRequest({ "reStart": val }); 

        if(val){
            loader.classList.add("display");
            // to stop loading after some time
            setTimeout(() => {
                loader.classList.remove("display");
                history.push('/config/0'); 
            }, 5000);
        } 

        fetch(`http://localhost:4200/uploadFirmwareConfirm`, requestOptions)
        .then(response => {
            if(response.json()){
                loader.classList.remove("display");
            }
        })
        .catch(err => console.error(err));       
        
        firmwareFile.current.value = '';
    }*/

    function firmwareChangeHandler(event){        
        const fileObject = event.target.files[0];
        if (!fileObject) return;
    
        let fileExtension = ((fileObject.name).split(".")).pop();
        if(fileExtension !== 'samq'){
            alert('Invalid File Data.!');
            firmwareFile.current.value = '';
        }
        else{            
            var reader = new FileReader();           
            reader.onload = function(e) { 
                let requestOptions = sendRequest({
                    "FileName": fileObject.name,
                    "uploadedFirmwareFile": [reader.result]
                });                
                fetch(`http://localhost:4200/uploadFirmware`, requestOptions)
                .then(response => response.json())
                .then((data) => {                   
                    console.log("Firmware upload successful.!");                     
                    //let isRestart = window.confirm("Firmware upload successful. Restart the unit to apply the update?");             
                    //restartCallback(isRestart);                  
                    alert("Firmware upload successful. The SAM-Q will now restart.");
                    firmwareFile.current.value = '';
                    history.push('/config/0');                    
                })
                .catch(err => console.error(err));
            }
            reader.readAsText(fileObject); 
        }

    }

    function upload() {
        firmwareFile.current.click();       
    }

    return (
        <React.Fragment>
            <div id='loading'></div>
            <div className="card-body">                
                <h5 className="card-title text-center">Update Firmware</h5>
                <hr />
                <div className='row'>
                    <div className='col-10 col-sm-10 col-md-10	col-lg-10 col-xl-10 col-xxl-10'>
                        <p className='px-2'>
                            <span className='text-dark fw-bold'>WARNING : </span>
                            Selecting a firmware update file will initiate the upgrade process and cause the unit to restart. Do not perform this operation whilst the unit is in use. 
                        </p>
                    </div>
                    <div className='col-2 col-sm-2 col-md-2	col-lg-2 col-xl-2 col-xxl-2 text-end'>
                    <input type="file" ref={firmwareFile} accept=".samq" className="d-none" onChange={firmwareChangeHandler} onClick={firmwareChangeHandler}/>                               
                        <button className='btn btn-secondary py-1' style={{marginLeft: '10%'}} onClick={()=>upload()} disabled={isAuthenticated ? false : true}>
                            Upload Firmware
                        </button> 
                    </div>
                </div>                                       
            </div>
        </React.Fragment>
    );
};

export default Uploadfirmware;