import React, {useEffect, useState} from 'react';
import {Route, Redirect, Switch, useHistory} from 'react-router-dom';
import Header from './Header';
import Footer from './Footer';
import Config from './Config';
import Status from './Status';
import Logs from './Logs';
import SidebarNav from './SidebarNav';
import { Id } from '../context/Id';
import { AuthContext } from '../context/AuthContext';
import './responsive.css';
/***********Initialise Functional Component React Routing******************************************************/
const Routing = () => {
    const history = useHistory();   
    const [ configId, setConfigId ]= useState(0);
    const [ isAuthenticated, setIsAuthenticated ] = useState(false);
    useEffect(()=>{
        history.push('/config/0'); 
    }, [history]);

    return(
        <Id.Provider value={[configId, setConfigId ]}>
            <AuthContext.Provider value={[ isAuthenticated, setIsAuthenticated ]}>
                <div className="container-fluid">
                    <Header/>
                        <div className='row'>
                            <div className='col-2 col-sm-3 col-md-3	col-lg-2 col-xl-2 col-xxl-2 px-0 my-3 mb-0' style={{backgroundColor: '#eeeded'}}>
                                <SidebarNav />
                            </div>
                            <div className='col-10 col-sm-9 col-md-9 col-lg-10 col-xl-10 col-xxl-10 px-3 bg-gary container-height'>
                                <Switch>                                          
                                    <Route exact path="/config/:configId" component={Config}/>              
                                    <Route path="/status" component={Status}/>
                                    <Route path="/logs" component={Logs}/>
                                    {/* default redirect to home page */}
                                    <Route exact path="/" component={Config}/>
                                    <Redirect from="*" to="/" />                                
                                </Switch>                            
                                <Footer/>
                            </div>
                        </div>            
                </div>
            </AuthContext.Provider>
        </Id.Provider>
      )
}

export default Routing;

