import React from 'react';

const LogList = (props) => {
    const saveToFileHandler = (Serial_Number) =>{
        props.cToPSaveToFileHandler(Serial_Number);
    }
    const renderList = ({logList}) => {
        if(logList){
            return logList.map((data) => {
                return(               
                    <div key={data.Serial_Number} className="card rounded-0" style={{backgroundColor: '#e7e8e9'}}>
                        <div className="row">
                            <div className="card-body" style={{height: '725px', overflowY: 'scroll'}}>
                                {data.log.map((logL) => {
                                    return(
                                    <p key={logL.toString()} className="mb-0 px-2">
                                        {logL}
                                    </p>
                                    )
                                })}                               
                            </div>
                            <div className='card-footer text-end'>
                                <button className='btn btn-secondary py-1' onClick={()=>saveToFileHandler(data.Serial_Number)}>Save to File</button>
                            </div>
                        </div>
                    </div>             
                )  
            })
        }  
    }

    return(
        <React.Fragment>
            {renderList(props)}
        </React.Fragment>
    )
}

export default LogList;