import React, {useState, useEffect, useRef, useContext} from "react";
import { Link, useHistory } from 'react-router-dom';
import './config.css';
import ConfigDetails from "./ConfigDetails";
import { Id } from "../context/Id";
import { AuthContext } from '../context/AuthContext';
const url = "http://localhost:4200/config";
/***********Initialise Functional Component React******************************************************/
const Config = () => {
  const history = useHistory();
  const [configId, setConfigId ] = useContext(Id); 
  const [isAuthenticated,  ] = useContext(AuthContext);
  const inputFile = useRef();
  const [config, setConfig] = useState([]);
  const [data, setData] = useState("");
  
  useEffect(()=>{
    fetch(url,{method:'GET'})
    .then((response) => response.json())
    .then((data) => setConfig(data))
    .catch(err => console.error(err));  
  }, []);

  /*****************Upload File Functionality******************************************************* */
  function uploadFileAction(selectedid, dumpdata) {
    if(selectedid === dumpdata.id){   
        setData(data => dumpdata);
    }
    inputFile.current.click();
  }
  
  const text2json = (contents) =>{ // returns json file format
    let cells = contents.split('\n').map(function (el) { return el.split(/\s+/); });
    let obj = {}, idx = 0, mode = 0;
    obj["id"] = 0;    //because array rest api work on id
    obj["RoutMode"] = [0, 4];
    obj["ProbeMode"] = [0, 1];
    let _routerArray = [], _pageArray = [], _modeArray = [], _videoArray = [], _inputNamesArray = [], _pgArray = [], _loudArray = [], _loudnessArray = [], _outArray = [], _inputArray = [], _hwArray = [], eng_name = [[],[],[],[]];
    
    let hw_object = [{Index:idx, Eng_Name:eng_name[0]},{Index:idx, Eng_Name:eng_name[1]},{Index:idx, Eng_Name:eng_name[2]},{Index:idx, Eng_Name:eng_name[3]}];
    let channel_obj = [{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []},{Mode: mode, Channel : []}];
    
    
    function assignNamedValuesToArray(names, values, emptyArray){
      for(let index=0; index < values.length; index++) {
        if( index !== 0){
          if(index){
            emptyArray.push(Number(values[index]));
          }
        }
      }
      return emptyArray;
    }
    
    function assignStringToArray(values,emptyArray){
      for(let index=0; index < values.length; index++) 
      {
        emptyArray.push(Number(values[index]));						
      }
      return emptyArray;
    }

    function assignValuesToArray(attrKey, values, _type){
      for(let index=0; index<values.length; index++){
        if((index !== 0) && (index !== 1)){
          if(_type === 'string'){
            if(index === (values.length -1)){
              attrKey += values[index];
            }
            else{
              attrKey += values[index] + ' ';
            }
          }
          else if(_type === 'boolen'){
            attrKey = (values[index] === "1") ? true : false;
          }
          else {
            attrKey = (values[index] === "1") ? true : false;
          }
        }
      }
      return attrKey;					
    }
  
    function assignNamedValuesToObject (names, values, object){
      for(let index=0; index < values.length; index++) {
        if( names[index].length !== 0)
        { 
          object[names[index]] = (typeof values[index] !== "number") ?  values[index] : Number(values[index]);
        }
      }
      
      return object;
    }
    
    
    cells.forEach(function (el, index) {			
      if(el[0] !== "#" && el.length > 1) {					
        if(isNaN(Number(el[1]))){
          if(String(el[1]).indexOf(".") !== -1){								
            let list = String(el[1]).split(".");
            if(list.length > 1 && list.length === 4){
              let arr = assignStringToArray(list, []);								
              obj[el[0]] = arr;
            }
            else{								
              obj[el[0]] = String(el[1]);
            }
          }
          else{
            //console.log(el);
            //console.log(el.length);
            if(el.length < 3){									
              obj[el[0]] = String(el[1]);
            }
            else if((el.length === 3) && ((el[0]) !== 'Inputs')){
            
              if(el[0] === 'Router_names'){
                let bool;																		
                _routerArray.push(assignValuesToArray(bool, el, 'boolen'));																					
                obj[el[0]] = _routerArray;
              }
              else if(el[0] === 'Pages_enabled'){
                let bool;																		
                _pageArray.push(assignValuesToArray(bool, el, 'boolen'));																					
                obj[el[0]] = _pageArray;
              }
              else if((el[0]) === 'Modes_enabled'){
                let bool;									  
                _modeArray.push(assignValuesToArray(bool, el, 'boolen'));									
                obj[el[0]] = _modeArray;
              }
              else if((el[0]) === 'Video_sources'){
                let bool;									  
                _videoArray.push(assignValuesToArray(bool, el, 'boolen'));									 
                obj[el[0]] = _videoArray;
              }
              else if((el[0]) === 'Input_Name'){
                let str = "";									  
                _inputNamesArray.push(assignValuesToArray(str, el, 'string'));									 
                obj[el[0]] = _inputNamesArray;
              }										
            
            }
            else{
              if((el[0]) === 'Page_Name'){
                let str = "";									  
                _pgArray.push(assignValuesToArray(str, el, 'string'));									
                obj[el[0]] = _pgArray;
              }
              else if((el[0]) === 'Loudness_Name'){
                 let str = "";									  
                _loudArray.push(assignValuesToArray(str, el, 'string'));									
                obj[el[0]] = _loudArray;
              }
              else if((el[0]) === 'Loudness'){
                let loudnessobject = {Probe_Config:0, Num_0:255, Num_1:255, Num_2:255, Num_3:255, Num_4:255, Num_5:255, Num_6:255, Num_7:255, Num_8:255, Num_9:255, Num_10:255, 
                Num_11:255, Num_12:255, Num_13:255, Num_14:255, Num_15:255, Reference_Level:0, Down_Mix:0, Probe_Running: 0};
                const loudnessNames = [ "", "", "Probe_Config", "Num_0", "Num_1", "Num_2", "Num_3", "Num_4", "Num_5", "Num_6", "Num_7", "Num_8", "Num_9", "Num_10", "Num_11",
                  "Num_12", "Num_13", "Num_14", "Num_15", "Reference_Level", "Down_Mix", "Probe_Running"];
                let loudnessValues = [];
                for(let y=0; y<el.length; y++) {										
                  loudnessValues = ["", "", Number(el[2]), Number(el[3]), Number(el[4]), Number(el[5]), Number(el[6]), Number(el[7]), Number(el[8]), Number(el[9]), Number(el[10])
                  , Number(el[11]), Number(el[12]), Number(el[13]), Number(el[14]), Number(el[15]), Number(el[16]), Number(el[17]), Number(el[18]), Number(el[19]), 
                  (el[20] === undefined)? 0 : Number(el[20]), (el[21] === undefined)? 0 : Number(el[21])];
                }
              
                _loudnessArray.push(assignNamedValuesToObject(loudnessNames, loudnessValues, loudnessobject));										
                obj[el[0]] = _loudnessArray;									
              }
              else if((el[0]) === 'Output'){									
                let myObject = {Src1:0, Src2:0, Lvl:0, Mute:false, Stereo:false, Solo: false, Pad:0, Pan:0, Op:0};
                const myNames = [ "", "", "Src1", "Src2", "Lvl", "Mute", "Stereo", "Solo", "Pad", "Pan", "Op" ];
                let myValues = [];
                for(let y=0; y<el.length; y++){
                  myValues = [ "", "", Number(el[2]), Number(el[3]), Number(el[4]), ((Number(el[5]) === 1)? true : false), ((Number(el[6]) === 1)? true : false), ((Number(el[7]) === 1)? true : false), Number(el[8]), Number(el[9]), Number(el[10]) ];									
                }

                _outArray.push(assignNamedValuesToObject(myNames, myValues, myObject));
                obj[el[0]] = _outArray;	
              }
              else if((el[0]) === 'Inputs'){						
                let str = "", name = [], offset = [], count = [];
                let inputsobject = {Name:name, Offset:offset, Count:count};

                for(let y=0; y<el.length; y++){
                  if((y !== 0) && (y !== 1)){
                    str += el[y];
                  }
                }
                let List = String(str).split(",");
                for(let i=0; i<List.length; i++) {										
                  if(List.length === 4){
                    if(i === 0){
                    name.push(List[i]);
                    inputsobject.Name = name;
                    }else if(i === 1){
                    offset.push(List[i]);
                    inputsobject.Offset = offset;
                    }else if(i === 2 || i === 3){
                    count.push(List[i]);
                    inputsobject.Count = count;
                    }
                  }else{
                    if(i === 0 || i === 1){
                    name.push(List[i]);
                    inputsobject.Name = name;
                    }else if(i === 2){
                    offset.push(List[i]);
                    inputsobject.Offset = offset;
                    }else if(i === 3 || i === 4){
                    count.push(List[i]);
                    inputsobject.Count = count;
                    }
                  }
                }
                _inputArray.push(inputsobject);									
                obj[el[0]] = _inputArray;						
              }
              else if ((el[0]) === 'Input_HW_Name'){
                let str = "";
                for (let y=0; y<el.length; y++) {
                  if(y === 1){
                    var spaceVariable = String(el[y]).replace(/\s/g,"");
                    var findIndex1 = String(spaceVariable).replace(/\[/g,"");
                    var findIndex2 = String(findIndex1).replace(/\]/g," ");
                    var idList = String(findIndex2).split(" ");

                    idx = parseInt(idList[0]); // Hardware port number
                    mode = 0;
                    if (parseInt(idList[1]) === 1) {
                    mode = 1; // Operating mode
                    }
                  }
                  else if ((y !== 0) && (y !== 1)) {
                    if(y === (el.length -1)){
                    str += el[y];
                    }
                    else{
                    str += el[y] + ' ';
                    }
                    //console.log(str);
                  }
                }
                channel_obj[2*idx + mode].Channel.push(str);
                channel_obj[2*idx + mode].Mode = mode;			  	
              }									
            }							

          }

        }
        else{
          //console.log(el);
          if((el[0]) === 'Input_Mode'){
            let Input_Mode = assignNamedValuesToArray('Input_Mode', el, []);								
            obj[el[0]] = Input_Mode;
          }
          else{
            obj[el[0]] = isNaN(Number(el[1])) ? el[1] : +el[1];
          }
        }
      }
    })
    var array_indexes = [0,1,2,3,4,6], countIndex = 0;
    for (let index=0; index < array_indexes.length; index++) {

      if(channel_obj[array_indexes[index]].Channel.length > 2){
      eng_name[countIndex].push(channel_obj[array_indexes[index]]);
      }
      else{
      eng_name[countIndex].push(channel_obj[array_indexes[index]]);
      countIndex = countIndex + 1;
      }

      if((index%2) === 1){
      countIndex = countIndex + 1;
      }
    }
    for (let i=0; i < 4; i++) {
      hw_object[i].Index = i;
      hw_object[i].Eng_Name = eng_name[i];
      _hwArray[i] = hw_object[i];
    }
    obj['Input_HW_Name'] = _hwArray;
    
            
    if ( !('IP_Address' in obj) ){
    /************ IP Address Missing! ****************************************************/
      obj["IP_Address"] = [0, 0, 0, 0];//check and get SAM-Q config IP settins
      obj["IP_Subnet"]  = [0, 0, 0, 0];
      obj["IP_Gateway"] = [0, 0, 0, 0];
    }
    if (!("Unit_Name" in obj) ){
    /************ Unit_Name Missing! ****************************************************/
      obj["Unit_Name"]  = "TSL SAM-Q";//check and get SAM-Q config IP addr
    }
    if ( !('SAM_Q_Edit_Version' in obj) ){
    /************ SAM_Q_Edit_Version Missing! ****************************************************/
      obj["SAM_Q_Edit_Version"] = "1.0A";//check and get SAM-Q Ver number
    }
    
    var json = JSON.stringify(obj, null, " ");
    //console.log('Obj file type: ' + typeof(obj));
    if(typeof json === "string"){
      //console.log(json);
      return json;
    }			
  }

  const renderFile = (fileObject) =>{
      var reader = new FileReader();
      let found = false;

      reader.onload = function(e) {
          var fileContent = reader.result;
          //Here the content has been read successfuly
          var cells = (fileContent).split('\n').map(function (el) { return el.split(/\s+/); });
              cells.forEach(function (el, index) {
                  if(el[0] === "SAM_Q_Config_Version"){
                      found = true;
                  }
              });

              if(found){
                    //console.log(data, fileContent, fileObject)
                    //props.useUploadFileId(Data, fileContent, fileObject); 
                    let Obj = {
                      "id": data.id,
                      "title": (fileObject.name).split(".")[0],
                      "body": data.body,
                      "loadActive": 1,
                      "uploadedFile": [JSON.parse(text2json(fileContent))]
                    }
              
                    const requestOptions = {
                        method: 'PUT',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify(Obj)
                    };
                
                    fetch(`${url}/${data.id}`, requestOptions)
                    .then(response => response.json())
                    .then((data) => {                    
                      let newArr = [...config];
                      newArr[data.id] = data;
                      setConfig(newArr);
                      console.log("File is uploaded.!");
                    })
                    .catch(err => console.error(err));
              }
              else{
                  alert('Invalid File Data.!');
                  //let element = document.getElementById('DefaultLoad');
                  //element.click();
              }

      }

      reader.readAsText(fileObject); 
  }
  function changeHandler(event){
      //alert('changeHandler')
      const fileObject = event.target.files[0];
      if (!fileObject) return;
      renderFile(fileObject); 
  };

  /******************Push Button Functionality******************************** */
  function mDown(e, text){
      if(e.target.parentElement.className === "btn btn-default"){
          e.target.parentElement.className = "btn btn-default active";
          //props.useActiveSlot(e.target.parentElement.id, text);
         
          let Obj = {
            "fileName": text,
            "pinCode": "1234"
          }
          const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(Obj)
          };
          fetch(`http://localhost:4200/activeConfig`, requestOptions)
          .then(response => response.json())
          .catch(err => console.error(err));        
      }
  }
  function mUp(e){
      if(e.target.parentElement.className === "btn btn-default active"){
          e.target.parentElement.className = "btn btn-default";
      }        
  }

  /*******************Download File******************************************************** */
  const json2text = (content) =>{
    var sqeVer = content.SAM_Q_Edit_Version; //sam-q-edit version
    var text = "", count=0;

      Object.keys(content).forEach(function eachKey(key) {
      if((key !== 'id') && (key !== 'RoutMode') && (key !== 'ProbeMode')){
        if(typeof content[key] === 'object'){
          var arryStr ="";
          if(content[key].constructor === Array){
            for (var x=0; x < content[key].length; x++) {
              if(typeof content[key][x] === 'number'){
                if(x === content[key].length-1){
                  arryStr += content[key][x];
                }
                else{
                  if(key === "Input_Mode"){
                    arryStr += content[key][x] + ' ';
                  }
                  else{
                    arryStr += content[key][x] + '.';
                  }
                }									
              }
              else if(typeof content[key][x] === 'string'){
                text += key + ' [' + x + '] ' + content[key][x] + '\n';
              }
              else if(typeof content[key][x] === 'boolean'){
                if(content[key][x] === true){
                  text += key + ' [' + x + '] ' + 1 + '\n';
                }
                else{
                  text += key + ' [' + x + '] ' + 0 + '\n';
                }										
              }
              else if(typeof content[key][x] === 'object'){
                let index = content[key][x], outputStr = "", tempObj;
                if(key === "Inputs"){
                  Object.keys(index).forEach(function eachKey(keys, values) {
                    if(typeof index[keys] === 'object'){
                    if(values === 2){
                      outputStr += index[keys] + " ";
                    }
                    else{
                      outputStr += index[keys] + ",";
                    }
                    }
                    else{
                    outputStr += index[keys] + " ";
                    }
                  });
                }
                else if(key === "Input_HW_Name"){
                  Object.keys(index).forEach(function eachKey(keys, values) {
                    if(typeof index[keys] === 'object'){
                    if(values === 1){
                      tempObj = rtnListOfInputHWName(index[keys]);												 
                    }
                    else {
                      outputStr += index[keys] + ",";
                    }
                    }
                    else{
                    outputStr += ' [' + index[keys] + ']';
                    }
                  });

                  for(var i=0; i<tempObj.length; i++){
                    text += key + outputStr + tempObj[i] +'\n';
                  }
                }
                else{
                  Object.keys(index).forEach(function eachKey(keys, values) {											  
                    if(typeof index[keys] === 'boolean'){
                    if(index[keys] === true){
                      outputStr += 1 + " ";
                    }
                    else{
                      outputStr += 0 + " ";
                    }
                    }
                    else{
                    outputStr += index[keys] + " ";
                    }
                  });
                }
      
                if(key !== "Input_HW_Name"){
                  text += key + ' [' + x + '] ' + outputStr + '\n';
                }
              }									
            }
            count++;
          }




          //------------------------------------------------IP Settings--------------------------------------------
          //IP settings should not be written out to the downloaded config file
          if(arryStr !== ""){
            if (count === 2){ //without the counter this text is printed out more than once because of the loop
            text += '# SAM_Q_Edit_Version ' + sqeVer + '\n';
            count = 0;
            }
            if (key !== "IP_Address" ){
            if (key !== "IP_Subnet"){
              if (key !== "IP_Gateway"){
              text += key + ' ' + arryStr + '\n';
              }
            }
            }           
          }

          function rtnListOfInputHWName(indexArray) {								
            var emptyValArray = [];
            if(indexArray.constructor === Array){

              for(var list=0; list<indexArray.length; list++){
                if(typeof (indexArray[list]) === 'object'){
                  let contentList = indexArray[list], str = "", empArray = [];
                  Object.keys(contentList).forEach(function eachKey(keys, values) {
                    if(typeof contentList[keys] === 'object'){
                      if(values === 1){
                        if(contentList[keys].constructor === Array){
                        for(var i=0; i<contentList[keys].length; i++){
                          empArray.push('[' + i + '] ' + contentList[keys][i]);
                        }
                        }
                      }
                    }
                    else{
                      str += '[' + contentList[keys] + ']';
                    }
                  });
                
                  for(var i=0; i<empArray.length; i++){
                    emptyValArray.push(str + empArray[i]);
                  }

                }

              }
            }														
            return emptyValArray;
          }
          
          
        }
        else{
          if ( key !== "SAM_Q_Edit_Version"){  // For this version we don't want these values printed out in download file
            if (key !== "Unit_Name"){
            text += key + ' ' + content[key] + '\n';
            }
          }
        }						
      }
      });				 
      return text;
  }
  const downloadPullFile = (id) =>{
      fetch(`${url}/${id}`,{method:'GET'})
      .then(response => response.json())
      .then((data)=> {

          if(data.uploadedFile.length !== 0){
              var Object = json2text(data.uploadedFile[0]);
              var blob = new Blob([ Object ], { type: 'text/config;charset=utf-8' }); ///{type: "octet/stream"}
              var fName = data.title + '.CONFIG';
          
              var url = window.URL.createObjectURL(blob);
              var a = document.createElement('a');
              a.href = url;
              a.download = fName;
              document.body.appendChild(a);
              a.click();
              window.URL.revokeObjectURL(url);
              document.body.removeChild(a);
          }
          else{
              alert('No File is available.!')
          }
      })
      .catch(err => console.error(err));
  }

  /***********Card Click Handler******************************************************/
  const handleClick = (event, title, id) => { 

    if(isAuthenticated){
      if(((title).split(".")[0]) !== "Empty Slot"){
        setConfigId(id); 
        history.push(`/config/${id}`);  
      }
    }
    else{
      if(((title).split(".")[0]) !== "Empty Slot"){
        setConfigId(id); 
        history.push(`/config/${id}`);  
      }
      else{
        event.preventDefault();
      }  
    }
  
  };



  const renderList = config.map((data) => {
    
    return (
      
          <div key={data.id} className='col-3 col-sm-3 col-md-3	col-lg-3 col-xl-3 col-xxl-3 mb-5'>
              <div onClick={(event) => handleClick(event, data.title, data.id)} className={"card rounded-0" + (data.id === configId ? " active_item" : "")}>
                  <Link to={`/config/${data.id}`} className={((data.title).split(".")[0]) !== "Empty Slot" ? '' : 'disabled-link'}  >
                    <div className="card-body slot-body">
                        <h6 className="card-title mb-2 fw-bold">{data.title}</h6>
                        <hr />
                        <p className="card-text">{data.body}</p>                              
                    </div>
                  </Link>
                  <div className="card-footer py-0">

                      <div className="row">
                          <div className="col-4 text-center fileIconCss">                                   
                              <button onClick={() => downloadPullFile( data.id )} className="btn btn-default" disabled={(isAuthenticated && ((data.title).split(".")[0]) !== "Empty Slot") ? false : true}>
                                  <span className="material-icons">file_download</span>
                              </button>
                          </div>
                          <div className="col-4 text-center fileIconCss">
                              <input id={`avtar_${data.id}`} name={`avtar_${data.id}`} type="file" ref={inputFile} accept=".config" className="d-none" onChange={changeHandler} onClick={changeHandler}/>
                              <button onClick={() => uploadFileAction( data.id, data )} className="btn btn-default" disabled={isAuthenticated ? false : true}>                                                
                                  <span className="material-icons">file_upload</span>
                              </button>
                          </div>
                          <div className="col-4 text-center fileIconCssNone">
                              <button className="btn btn-default" id={`activeSlot_${data.id}`} disabled={(isAuthenticated && ((data.title).split(".")[0]) !== "Empty Slot")  ? false : true} onMouseDown={(e) => mDown(e, data.title)} onMouseUp={(e)=>mUp(e)}>
                                  <span className="material-icons">check_circle</span>
                              </button>
                          </div>
                      </div>

                  </div>
              </div>
          </div>
    );
  });
  return (
    <div className='row p-3'>
      <React.Fragment>
        {renderList}
      </React.Fragment>
   
      <ConfigDetails data={config} />
      
    </div>
  );
};

export default Config;