import React , {useRef, useState, useContext, useEffect} from 'react';
import {Button, Modal} from 'react-bootstrap';
import login from '../icons/Icon_Login_16px.png';
import { AuthContext } from '../context/AuthContext';
const url = "http://localhost:4200/activeConfig";

/*********** Initialize Functional Component React******************************************************/
const LoginButton = () => {
  const [, setIsAuthenticated ] = useContext(AuthContext);
  const [pin, setPin] = useState(null);
  const [isDisabled, setDisabled] = useState(true);
  const inputPin = useRef(null);
  const errorMessageRef = useRef();
  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  useEffect(() => {
      fetch(url,{method:'GET'})
      .then((response) => response.json())
      .then((data) => {
          setPin(data.pinCode);
      })           
  }, []);

/***********Login Functionality ******************************************************/
  const loginHandler = () =>{   
    if(inputPin.current.value === pin && inputPin.current.value !== undefined){
      setIsAuthenticated(true);        
      handleClose(); 
    }
    else{      
      setIsAuthenticated(false);
      inputPin.current.value = '';
      setDisabled(true);
      alert('Pin Incorrect..!');
    } 
  }
/***********Pin Code Handler******************************************************/  
  const keyDownHandler = (e) => {    
      if (49 > e.which || e.which > 56)
      if (97> e.which || e.which > 104)
      if (e.key.length === 1)
        e.preventDefault();

      if(e.which === 13 && (inputPin.current.value.length === 4)){      
        loginHandler();
      } 
  }

  const inputHandler = () => {
    if(inputPin.current.value.length < 4){
      errorMessageRef.current.style.display = 'block';
      setDisabled(true);
    }
    else{
      errorMessageRef.current.style.display = 'none';
      setDisabled(false);
    }    
  }

  return (
    <>
      <button type="button" className='btn btn-default py-0 px-1 border-start border-2 fw-normal' onClick={handleShow} style={{borderLeft:'1px solid #b0b0b0!important', borderRadius: '0px'}}>
          <span className='px-2 fw-bold'>Login</span>
          <img src={login} alt='Icon Loading'/>
      </button>
      
      <Modal show={show} onHide={handleClose} backdrop="static" keyboard={false} aria-labelledby="contained-modal-title-vcenter" centered> 
        <Modal.Header className="border-bottom-0" closeButton>
            <h5 className="modal-title" id="loginModalLabel">Login</h5>
        </Modal.Header>
        <Modal.Body className="py-1"> 
          <div className="mb-3 row">
            <label htmlFor="pin" className="col-sm-3 col-form-label text-end">Pin : </label>
            <div className="col-sm-6">
              <input type="number" className="form-control rounded-0" id="pin" ref={inputPin} onBlur={inputHandler} onInput={(e) => e.target.value = e.target.value.slice(0, 4)} onKeyDown={keyDownHandler}/>
              <div className="invalid-feedback text-start" ref={errorMessageRef}>
                Please provide a valid pin.
              </div>
            </div>                    
          </div> 
        </Modal.Body>
        <Modal.Footer className="py-0 border-top-0 d-block">
          <div className="mb-3 row">
            <div className="col-sm-3"></div>
            <div className="col-sm-6 px-0 text-start">
              <Button type="submit" variant="default" className="rounded-0" style={{border:'1px solid #ced4da', marginLeft: '6px'}} onClick={loginHandler} disabled={isDisabled}>
                  <span className='px-2'>Login</span>
                  <img src={login} alt='Icon Loading'/>
              </Button>
            </div>
          </div>
        </Modal.Footer>            
      </Modal>

    </>
  );
};

export default LoginButton;