import React, {useContext} from 'react';
import { NavLink } from 'react-router-dom';
import './style.css';
import configIcon from '../icons/Tab Icon - Status_Black_16px.png';
import statusIcon from '../icons/Tab Icon - Settings_Black_16px.png';
import logIcon from '../icons/Tab Icon - Logging_Black_16px.png';
import { Id } from '../context/Id';
/***********Initialise Functional Component React- Sidebar Navigation******************************************************/
const SidebarNav = () => {
    const [ configId ] = useContext(Id);
    return(
        <div>
           <ul className="nav flex-column">            
                <li className="nav-item mb-1">
                    <NavLink exact activeClassName="active" to={`/config/${configId}`} className="nav-link tsl-nav-link">
                        <span className='px-2 fw-bolder'>Config</span>
                        <img src={configIcon} alt='Icon Loding'/>
                    </NavLink>                    
                </li>
                <li className="nav-item mb-1">
                    <NavLink exact activeClassName="active" to="/status" className="nav-link tsl-nav-link">
                        <span className='px-2 fw-bolder'>Status</span>
                        <img src={statusIcon} alt='Icon Loding'/>
                    </NavLink>
                </li>
                <li className="nav-item mb-1">
                    <NavLink exact activeClassName="active" to="/logs" className="nav-link tsl-nav-link">
                        <span className='px-2 fw-bolder'>Logs</span>
                        <img src={logIcon} alt='Icon Loding'/>
                    </NavLink>
                </li>          
            </ul>
        </div>
    )
}

export default SidebarNav;