import React, {useEffect, useState} from 'react';
import {Route, Switch} from 'react-router-dom';
import logo from '../tsl_logo.png';
import AuthenticationButton from './AuthenticationBtn';
const url = "http://localhost:4200/activeConfig";
/***********Initialise Functional Component React******************************************************/
const Header = () => {  
    const [name, setName] = useState("Config");  
    useEffect(() => {
        const interval = setInterval(() => {
            fetch(url,{method:'GET'})
            .then((response) => response.json())
            .then((data) => {
                let dumpName = (data.fileName).split(".")[0];
                setName(name => (dumpName !== "Empty Slot" ? dumpName : "Config"));               
            })            
        }, 100);
        return () => clearInterval(interval);
    }, []);

    return(
        <div className='row'>
            <div className='col-2 col-sm-3 col-md-3	col-lg-2 col-xl-2 col-xxl-2 tsl-nav-link px-0'>
                <a className="navbar-brand" href="https://www.tslproducts.com/" target="_blank" rel="noreferrer">
                    <img src={logo} alt='Logo Loding' width={'100%'} height={'90px'} style={{objectFit: 'cover'}}/>
                </a>
            </div>
            <div className='col-9 col-sm-6 col-md-7	col-lg-8 col-xl-8 col-xxl-9 py-4 px-3 mt-2'>
                <Switch>
                    <Route exact path="/" children={<h4 className='mb-0 f-heading'>{name}</h4>}/>          
                    <Route path="/config/:configId" children={<h4 className='mb-0 f-heading'>{name}</h4>}/>              
                    <Route path="/status" children={<h4 className='mb-0 f-heading'>Status</h4>}/>
                    <Route path="/logs" children={<h4 className='mb-0 f-heading'>Logs</h4>}/>                           
                </Switch>
            </div>
            <div className='col-1 col-sm-3 col-md-2	col-lg-2 col-xl-2 col-xxl-1 py-4 mt-2 text-center'>
                <AuthenticationButton />                
            </div>                            
        </div>
    )
}

export default Header;