import React, {Component} from 'react';
import StatusList from "./StatusList";
import Uploadfirmware from './Uploadfirmware';
//const url = "http://localhost:8080/v1_0/status";
const url = "http://192.168.39.35:8080/status";
/***********Initialise Class Component React******************************************************/
class Status extends Component {
    constructor(){
        super();
        this.state ={ 
            statusData : []
        }
        this.firmwareFile = React.createRef();
        this.getStatusRequest = this.getStatusRequest.bind(this);        
        this.getStatusRequest(url);
    }
    /***********Http Request - Get Method******************************************************/
    getStatusRequest(url){
        fetch(url,{method:'GET'})
        .then((response) => response.json())
        .then((data) => {
            this.setState({
                statusData : data
            });            
        })
        .catch(err => console.error(err));
    }

    /***********Set Interval on Every 5s******************************************************/
    componentDidMount() {
        this.timerID = setInterval(() => {
            this.getStatusRequest(url);                
        },5000);
    }
    /***********Clear Interval******************************************************/
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
    /***********Render Method of React******************************************************/
    render(){       
        return(
            <div className='row p-3'>
            
                <div className="card mb-3 rounded-0" style={{width: "40rem", borderLeft: "7px solid #6894ce"}}>
                    <div className="card-body">
                        <h5 className="card-title text-center">About</h5>
                        <hr />
                        {Object.keys(this.state.statusData).map((keyName, i) => (                                                      
                           <StatusList  key={i} attribute={[Object.keys(this.state.statusData)[i]]} Val={this.state.statusData[keyName]}/>                           
                        ))}                    
                    </div>
                </div>

                <div className='card rounded-0' style={{borderLeft: "7px solid #8bcb8c"}}>
                    <Uploadfirmware />                    
                </div>
            </div>
        )
    }
}

export default Status;