import React, {useContext} from 'react';
import logout from '../icons/Icon_Logout_16px.png';
import { AuthContext } from '../context/AuthContext';
/***********Initialise Functional Component React******************************************************/
const LogoutButton = () => {
  const [ isAuthenticated , setIsAuthenticated ] = useContext(AuthContext);
  
  /******************Louout Functionality******************************** */
  const logoutHandler = () =>{
    if(isAuthenticated){
      setIsAuthenticated(false); // Set Context
    }    
  }
  return (
    <button type="button" className='btn btn-default py-0 px-1 border-start border-2 fw-normal' onClick={logoutHandler} style={{borderLeft:'1px solid #b0b0b0!important', borderRadius: '0px'}}>
        <span className='px-2 fw-bold'>Logout</span>
        <img src={logout} alt='Icon Loding'/>
    </button>
  );
};

export default LogoutButton;