import React, {useState, useEffect, useContext, useCallback, useRef} from 'react';
import LoginButton from './LoginBtn';
import LogoutButton from './LogoutBtn';
import { AuthContext } from '../context/AuthContext';
import moment from 'moment'; // Moment Node module library for parsing, validating, manipulating, and formatting dates 

/***********Initialise Functional Component React******************************************************/
const AuthenticationButton = () => {
  const [isAuthenticated, setIsAuthenticated ] = useContext(AuthContext); 
  const [events, ] = useState(['click', 'load', 'scroll', 'mousemove', 'mousedown', 'keypress']);
  const [, setSecond] = useState(0);

  let timeStamp = useRef();
  let warningInactiveInterval = useRef();
  let startTimerInterval = useRef();

  // warning timer
  let warningInactive = useCallback((timeString) => {    
    clearTimeout(startTimerInterval.current);

    warningInactiveInterval.current = setInterval(() => {
      const maxTime = 2; // Maximum ideal time given before logout 
      const popTime = 1; // remaining time (notification) left to logout.

      const diff = moment.duration(moment().diff(moment(timeString)));
      const minPast = diff.minutes();
      const leftSecond = 60 - diff.seconds();

      if (minPast === popTime) { 
        setSecond(leftSecond);
      }

      if (minPast === maxTime) {
        clearInterval(warningInactiveInterval.current);
        sessionStorage.removeItem('lastTimeStamp');
        // your logout function here
        if (isAuthenticated) {
          setIsAuthenticated(false); 
        }
      }
    }, 1000);
  }, [isAuthenticated, setIsAuthenticated]);

  // start inactive check
  let timeChecker = useCallback(() => {
    startTimerInterval.current = setTimeout(() => {
      let storedTimeStamp = sessionStorage.getItem('lastTimeStamp');  
      warningInactive(storedTimeStamp);   
    }, 60000);
  }, [warningInactive]);

   // reset interval timer
  let resetTimer = useCallback(() => {
      clearTimeout(startTimerInterval.current);
      clearInterval(warningInactiveInterval.current);

      if (isAuthenticated) {
        timeStamp.current = moment().format();
        sessionStorage.setItem('lastTimeStamp', timeStamp.current);
      } else {
        clearInterval(warningInactiveInterval.current);
        sessionStorage.removeItem('lastTimeStamp');
      }
      timeChecker();
  }, [isAuthenticated, timeChecker]);


  useEffect(() => {
    events.forEach((event) => {
      window.addEventListener(event, resetTimer);
    });
    timeChecker();
    return () => {
      clearTimeout(startTimerInterval.current);       
    }
  }, [resetTimer, events, timeChecker]);

  return isAuthenticated ? <LogoutButton /> : <LoginButton />;  
};

export default AuthenticationButton;