import React, {Component} from 'react';
import LogList from './LogList';
const url = "http://localhost:4200/logs";
/***********Initialise Class Component React******************************************************/
class Logs extends Component {
    constructor(){
        super();
        this.state ={
            logs: ''
        }

        fetch(url,{method:'GET'})
        .then((response) => response.json())
        .then((data) => {
           this.setState({
                logs:data
            })            
        })
    }
    
    componentDidMount(){
        this._isMounted = true;
        setInterval( () => {
            fetch(url,{method:'GET'})
            .then((response) => response.json())
            .then((data) => {
                if (this._isMounted) {
                    this.setState({
                        logs:data
                    })
                }
            })
        },5000)
    }

    downloadLogsFile(serial_Num){
        function pad2(n) { return n < 10 ? '0' + n : n } 
        const timescalefun = (d) => {
            return d.getFullYear().toString() +
             pad2(d.getMonth() + 1) +
              pad2( d.getDate()) +
               pad2( d.getHours() ) +
                pad2( d.getMinutes() ) +
                pad2( d.getSeconds() );
        }

        var date = new Date();   
        let filename = 'SAM-Q-NET-Log-'+ serial_Num + '-' + timescalefun(date);
       
        var blob = new Blob([ this.loglist[0].log.join(',\n') ], { type: 'text/stream;charset=utf-8' }); ///{type: "octet/stream"}
        var url = window.URL.createObjectURL(blob);
        var a = document.createElement('a');
        a.href = url;
        a.download = filename + '.txt';
        document.body.appendChild(a);
        a.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(a);
    }

    componentWillUnmount() {
        this._isMounted = false;
        clearInterval(this.logs);
    }

    render(){
        return(
            <div className='row p-3'>
                <LogList logList={this.state.logs} cToPSaveToFileHandler={this.downloadLogsFile}/>
            </div>
        )
    }
}

export default Logs;