import React from 'react';
/***********Initialise Functional Component in React******************************************************/
/*********Return Input Element*****************/
const StatusList = (props) =>{
    return (
        <div className="row">
            <label htmlFor="Model" className="col-sm-4 col-form-label">{props.attribute}: </label>
            <div className="col-sm-8">
                <input type="text" readOnly className="form-control-plaintext" value={props.Val} />
            </div>
        </div>
    )
}

export default StatusList;